import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';

// 引入 Markdown 文件（Webpack 的 require.context 使得能够动态引入文章）
const requireContext = require.context('../articles', false, /\.md$/);

const Article = () => {
    const { id } = useParams(); // 获取文章 ID
    const [content, setContent] = useState('');

    useEffect(() => {
        // 根据 ID 动态加载相应的 Markdown 文件
        if (id) {
            try {
                const article = requireContext(`./${id}.md`);
                console.log(article);
                setContent(article.default); // 通过 article.default 获取文件内容
            } catch (error) {
                setContent('文章不存在'); // 如果找不到该文章文件，显示错误
            }
        }
    }, [id]);

    return (
        <section className="post-detail">
            <h2>文章内容</h2>
            {/* 使用 react-markdown 渲染 Markdown 内容 */}
            <ReactMarkdown>{content}</ReactMarkdown>
            <Link to="/" className="back-link">返回首页</Link>
        </section>
    );
};

export default Article;
